import React from 'react'
import cl from './Navbar.module.css'
import {NavLink} from "react-router-dom";
import Sidebar from "./Sidebar/Sidebar";
import SidebarContainer from "./Sidebar/SidebarContainer";

const Navbar = () => {
    return (
        <div className={cl.navBar}>
            <nav className={cl.nav}>
                <div className={cl.item}>
                    <NavLink to='/profile'
                             className={navData => navData.isActive ? cl.active : cl.item}>Profile</NavLink>
                </div>
                <div className={cl.item}>
                    <NavLink to="/dialogs"
                             className={navData => navData.isActive ? cl.active : cl.item}>Messages</NavLink>
                </div>
                <div className={cl.item}>
                    <NavLink to="news" className={navData => navData.isActive ? cl.active : cl.item}>News</NavLink>
                </div>
                <div className={cl.item}>
                    <NavLink to="/music" className={navData => navData.isActive ? cl.active : cl.item}>Music</NavLink>
                </div>
                <div className={cl.item}>
                    <NavLink to="/settings"
                             className={navData => navData.isActive ? cl.active : cl.item}>Settings</NavLink>
                </div>
                <div className={cl.item}>
                    <NavLink to="/users"
                             className={navData => navData.isActive ? cl.active : cl.item}>Find Friends</NavLink>
                </div>
                <SidebarContainer/>
            </nav>
        </div>
    );
}

export default Navbar
