import React from 'react'
import cl from './Header.module.css'
import {NavLink} from "react-router-dom";

const Header = (props) => {
    return (
        <header className={cl.header}>
            <img src="https://webmentor.gr/wp-content/uploads/2018/11/logo300X300.png"/>
            <h1 className={cl.headerTitle}>- Social Network</h1>
            <div className={cl.loginBlock}>
                {props.isAuth ? <div className={cl.login}>{props.login} - <button className={cl.logoutBtn} onClick={props.logout}>Logout</button></div>
                :<NavLink to={'/login'}>Login</NavLink>}
            </div>
        </header>
    )
}

export default Header
