import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";

let SET_USER_DATA = 'auth/SET_USER_DATA'

let initialState = {
    id: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false,
    password: null,
    rememberMe: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        default:
            return state
    }
}

export const setAuthUserData = (userId, email, login, isAuth) => ({type: SET_USER_DATA, payload:
        {userId, email, login, isAuth}})

export const getAuth = () => async (dispatch) => {
        let data = await authAPI.getAuthData()
                if(data.resultCode === 0) {
                    let {login, id, email} = data.data
                    dispatch(setAuthUserData(id, email, login, true))
                }

}

export const login = (email, password, rememberMe) => async (dispatch) => {
    let data = await authAPI.createAuthLogin(email, password, rememberMe)
            if(data.resultCode === 0) {
                dispatch(getAuth())
            } else {
                let message = data.messages.length > 0 ? data.messages : "Some error"
                dispatch(stopSubmit("login", {_error: message}))
            }
}

export const logout = () => async (dispatch) => {
    let data = await authAPI.deleteAuthLogin()
            if(data.resultCode === 0) {
                dispatch(setAuthUserData(null, null, null, false))
            }
}

export default authReducer