const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {
    messagesData: [
        {id: 1, message: 'Hi!', from: false},
        {id: 2, message: 'How are you?', from: true},
        {id: 3, message: 'Yo', from: false},
        {id: 4, message: 'Yo', from: true},
        {id: 5, message: 'Yo', from: false},
    ],
    dialogsData: [
        {id: 1, name: 'Dimych'},
        {id: 2, name: 'Andrey'},
        {id: 3, name: 'Sveta'},
        {id: 4, name: 'Sasha'},
        {id: 5, name: 'Viktor'},
        {id: 6, name: 'Valera'},
    ]
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            return {
                ...state,
                messagesData: [...state.messagesData, {id: 6, message: action.newMessageText, from: true}]
            }
        }
        default:
            return state

    }
}

export const addMessageActionCreator = (newMessageText) => {
    return {
        type: ADD_MESSAGE,
        newMessageText
    }
}

export default dialogsReducer;